import Editor from './src/Editor/Editor.html';
import { DEFAULT_STYLE, style_to_old, style_from_editor, style_from_gmx } from './src/Utils.js';
import Translations from 'scanex-translations';
import Coloring from './src/Coloring/Coloring.html';

Translations.addText ('eng', {
    style: {
        new: 'New style',
        default: 'Unnamed style'
    }
});

Translations.addText ('rus', {
    style: {
        new: ' ',
        default: '  ',
    }
});

const translate = Translations.getText.bind(Translations);

window.nsGmx = window.nsGmx || {};

const pluginName = 'GmxStyler';
let editor = null;
let editorContainer = null;
let coloring = null;
let coloringContainer = null;
let prevStyles = [];

const create_editor = (layerId, styleIndex, initialStyles) => {
    let layer = nsGmx.gmxMap.layersByID[layerId];        
    let layersTreeContainer = nsGmx.layersTreePane.children[0];
    layersTreeContainer.style.display = 'none';
    
    editorContainer = document.createElement('div');
    editorContainer.className = 'gmx-styler';
    nsGmx.layersTreePane.appendChild(editorContainer);
    
    let gmxProps = layer.getGmxProperties && layer.getGmxProperties();
    const {attributes, attrTypes, GeometryType, title} = gmxProps;    
    let legendFields = attributes.slice();
    legendFields.unshift('');
    
    const styles = initialStyles ? initialStyles.slice() : layer.getStyles().map (s => style_from_gmx(s, GeometryType));
    editor = new Editor({
        target: editorContainer,
        data: {
            fonts: ['Roboto'],
            geometryType: GeometryType,                    
            legendFields,
            styles,
            selected: styleIndex,
            title,
        }
    });
    editor.on('state', ({changed, current}) => {
        if (!changed.selected || changed.styles) {
            Object.keys (DEFAULT_STYLE).forEach (k => {
                if (changed[k] && current.styles[current.selected].hasOwnProperty(k)) {
                    current.styles[current.selected][k] = current[k];
                }
            });
            const styles = current.styles.map(s => style_from_editor(s, GeometryType));                    
            layer.setStyles(styles);                    
            const oldStyles = styles.map(style_to_old);
            const props = {...gmxProps, ...{styles: oldStyles}};
            const div = $(window._queryMapLayers.buildedTree).find(`div[LayerID='${gmxProps.name}']`)[0];
            window._layersTree.findTreeElem(div).elem.content.properties = props;            
            window._mapHelper.updateTreeStyles(oldStyles, div, window._layersTree);
        }
    });
    editor.on ('close', () => {                        
        nsGmx.layersTreePane.removeChild(editorContainer);
        editor = null;
        layersTreeContainer.style.display = 'block';
    });
    editor.on ('coloring', () => {
        let {styles} = editor.get();
        prevStyles = styles.slice();
        editor = null;                
        nsGmx.layersTreePane.removeChild(editorContainer);
        create_coloring (layerId);                           
    });    
};

const fmt_value = x => {
    switch (typeof x) {
        case 'number':
            return Math.round (x);
        default:
            return x;
    }
};

const partition_to_styles = (partition, column, type) => {
    const count = partition.length;
    return partition.map(({low, high, color}, i) => {
        let s = {...DEFAULT_STYLE};
        s.name = high ? `${fmt_value (low)} - ${fmt_value (high)}` : `${fmt_value (low)}`;
        s.decorationFill = color;
        s.useDecorationFill = true;
        s.decorationOutlineSize = 0;
        const rho = i === count - 1 ? '<=' : '<';
        switch (type) {
            case 'integer':
            case 'float':
                s.filter = high ? `[${column}] >= ${low} AND [${column}] ${rho} ${high}` : `[${column}] = ${low}`;
                break;
            case 'string':
            case 'datetime':
                s.filter = high ? `[${column}] >= '${low}' AND [${column}] ${rho} '${high}'` : `[${column}] = '${low}'`;
                break;
            default:
                break;
        }        
        return s;
    });
};

const create_coloring = layerId => {
    let layer = nsGmx.gmxMap.layersByID[layerId];
    let layersTreeContainer = nsGmx.layersTreePane.children[0];    
    layersTreeContainer.style.display = 'none'; 

    coloringContainer = document.createElement('div');
    coloringContainer.className = 'gmx-styler-coloring';    
    nsGmx.layersTreePane.appendChild(coloringContainer);

    let gmxProps = layer.getGmxProperties && layer.getGmxProperties(); 
    const {attributes, attrTypes, GeometryType, title} = gmxProps;            
    const columns = attributes.map((x,i) => {
        return {name: x, type: attrTypes[i]};
    });

    coloring = new Coloring({
        data: {
            columns,
            title,
        },
        layerID: layerId,
        target: coloringContainer,                        
    });    
    coloring.on('apply', ({partition, columns, columnIndex}) => {                        
        const {name, type} = columns[columnIndex];
        const editorStyles = partition_to_styles (partition, name, type);
        const styles = editorStyles.map (s => style_from_editor(s, GeometryType));
        layer.setStyles(styles);                    
        // const oldStyles = styles.map(style_to_old);
        // const props = {...gmxProps, ...{styles: oldStyles}};
        // const div = $(window._queryMapLayers.buildedTree).find(`div[LayerID='${gmxProps.name}']`)[0];
        // window._layersTree.findTreeElem(div).elem.content.properties = props;            
        // window._mapHelper.updateTreeStyles(oldStyles, div, window._layersTree);
    });
    coloring.on('save', ({partition, columns, columnIndex}) => {                        
        nsGmx.layersTreePane.removeChild(coloringContainer);
        coloringContainer = null;
        coloring = null;
        layersTreeContainer.style.display = 'block';

        const {name, type} = columns[columnIndex];
        const editorStyles = partition_to_styles (partition, name, type);
        create_editor (layerId, 0, editorStyles);

        const styles = editorStyles.map (s => style_from_editor(s, GeometryType));
        layer.setStyles(styles);                    
        const oldStyles = styles.map(style_to_old);
        const props = {...gmxProps, ...{styles: oldStyles}};
        const div = $(window._queryMapLayers.buildedTree).find(`div[LayerID='${gmxProps.name}']`)[0];
        window._layersTree.findTreeElem(div).elem.content.properties = props;            
        window._mapHelper.updateTreeStyles(oldStyles, div, window._layersTree);

               
    });
    coloring.on ('cancel', () => {
        nsGmx.layersTreePane.removeChild(coloringContainer);
        coloring = null;
        coloringContainer = null;   
        layersTreeContainer.style.display = 'block';

        create_editor (layerId, 0, prevStyles);        
        const styles = prevStyles.map (s => style_from_editor(s, GeometryType));
        layer.setStyles(styles);                    
        const oldStyles = styles.map(style_to_old);
        const props = {...gmxProps, ...{styles: oldStyles}};
        const div = $(window._queryMapLayers.buildedTree).find(`div[LayerID='${gmxProps.name}']`)[0];
        window._layersTree.findTreeElem(div).elem.content.properties = props;            
        window._mapHelper.updateTreeStyles(oldStyles, div, window._layersTree);
    }); 
};

const publicInterface = {
    pluginName,
    afterViewer: function (params, map) {
        if (window.nsGmx) {
            
            // window.nsGmx.gmxMap.layers.forEach(layer => {
            //     console.log(layer);
            //     // styleEditor.setHoverStyle(layer);
            // });

            // nsGmx.leafletMap.on('layeradd', e => {
            //     const layer = e.layer;
            //     console.log(layer);
            //     // styleEditor.setHoverStyle(layer);
            // });

            // replace existing LayersStylesEditor function
            const pluginPath = gmxCore.getModulePath(pluginName);
            window.gmxCore.loadScript(`${pluginPath}ckeditor/ckeditor.js`).done(() => {
                nsGmx.createStylesDialog = function(treeElem, treeView, styleIndex) {
                    let layerId = treeElem.name;
                    create_editor (layerId, styleIndex || 0);                                
                }
            });
            
        }
    },
    unload: function () {
        if (editor) {
            editor.destroy();
        }
    }
};

if (window.gmxCore) {
    const pluginPath = gmxCore.getModulePath(pluginName);
    window.gmxCore.addModule(pluginName, publicInterface, {
        css: 'gmx-styler.css',
        init: function(module, path) {}
    });
} else {
    window.nsGmx[pluginName] = publicInterface;
}